<template>
  <div>
    <tab-page ref="tabPage">
      <template slot="smsDayReport">
        <data-table :data-table="dataTable" show-summary ref="dataTable">
          <div slot="conditions" style="margin-bottom:15px">
            <el-form :inline="true" :model="queryParams">
              <el-form-item>
                <span>日期:</span>
                <el-date-picker
                  v-model="queryParams.startDate"
                  type="date"
                  placeholder="选择日期" size="small" style="width:170px">
                </el-date-picker>
                <el-time-select
                  v-model="queryParams.startTime"
                  :picker-options="timeOptions"
                  placeholder="选择时间" size="small" style="width:100px" >
                </el-time-select>
              </el-form-item>
              <span class="span-style">至</span>
              <el-form-item>
                <el-date-picker
                  v-model="queryParams.endDate"
                  type="date"
                  placeholder="选择日期" size="small" style="width:170px">
                </el-date-picker>
                <el-time-select
                  v-model="queryParams.endTime"
                  :picker-options="timeOptions"
                  placeholder="选择时间" size="small" style="width:100px">
                </el-time-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit" size="small" style="margin-top:0">查询</el-button>
              </el-form-item>
            </el-form>
          </div>
          <template slot="columns">
            <el-table-column prop="pageIndex" label="序号" width="70" sortable></el-table-column>
            <el-table-column prop="date" label="日期" width="100" :formatter="formatterDate" sortable></el-table-column>
            <el-table-column prop="time" label="时间" width="80" sortable></el-table-column>
            <el-table-column prop="account" label="账号" width="100" sortable></el-table-column>
            <el-table-column prop="smsType" label="主要短信类型" width="120" sortable></el-table-column>
            <el-table-column prop="pdlsendCount" label="PDL发送条数" width="120" sortable></el-table-column>
            <el-table-column prop="pdlweSuccessRate" label="PDL-we成功率" width="90" sortable></el-table-column>
            <el-table-column prop="weThirdSuccessRate" label="we-三方成功率" width="90" sortable></el-table-column>
            <el-table-column prop="pdlThirdAverageSendTime" label="PDL-三方平均发送时间" width="120" sortable></el-table-column>
            <el-table-column prop="sendSuccessCount" label="触达统计条数" width="120" sortable></el-table-column>
            <el-table-column prop="sendSuccessRate" label="触达成功率" width="120" sortable></el-table-column>
            <el-table-column prop="sendSuccessDetail" label="触达详情统计" width="120" sortable></el-table-column>
            <el-table-column prop="sendSuccessAverageTime" label="平均触达时间" width="120" sortable></el-table-column>
            <el-table-column prop="accountAmount" label="账户余额" width="100" sortable></el-table-column>
            <!--<el-table-column fixed="right" label="操作" width="180">-->
            <!--<template slot-scope='scope'>-->
            <!--<el-button type="text" size="small" @click="openDetailPage(scope.row, scope.column)" v-permission="'auditprepay:detail'">查看详情</el-button>-->
            <!--</template>-->
            <!--</el-table-column>-->
          </template>
        </data-table>
      </template>
    </tab-page>
  </div>
</template>
<style scoped>
  .el-form-item__content .el-button {
    margin-top: 20px;
  }

  .el-table--border th {
    text-align: center;
  }

  .el-table th .cell {
    text-align: center;
  }

  .el-form--inline .el-form-item {
    margin-right: 2px;
  }

  .el-form-item {
    margin-bottom: 2px;
  }

  .el-dialog__footer {
    padding: 10px 0px;
    text-align: center;
  }
  .timeSelect{
    width:100px;
  }
</style>
<script>
  import common from '../../../utils/common'
  import formatDate from '../../../utils/formatDate'
  import DataTable from "../../../components/DataTable"
  import TabPage from "../../../components/TabPage"
  export default{
    components: {
      DataTable,
      TabPage,
    },
    data(){
      return {
      	timeOptions:{
          start: '00:00',
          step: '01:00',
          end: '23:00'
        },
        queryParams: {
          startDate: '',//开始日期
          startTime:'',//开始时间
          endDate:'',//结束日期
          endTime: '',//结束时间
        },
        dataTable: {
          pageSize: 1000,
          pageSizes: [10, 20, 50, 100, 1000],
          url: "/test/api/v1/smsList",
          emptyText: '没有记录'
        },
      }
    },
    mounted(){
      var defaultPage = {label: "短信日报", name: "smsDayReport", slotName: 'smsDayReport', closable: false};
      this.$refs.tabPage.$emit("open-tab", defaultPage);
    },
    methods: {
      loadLists(){
        this.$refs.dataTable.$emit('load-data');
      },
      onSubmit: function () {
        this.dataTable.queryParams = this.queryParams;
        var tag = this.warnTime(this.dataTable.queryParams.startTime, this.dataTable.queryParams.endTime);
        if (tag) {
          return;
        }
        this.dataTable.queryParams.startTime = this.dealTime(this.dataTable.queryParams.startTime);
        this.dataTable.queryParams.endTime = this.dealTime(this.dataTable.queryParams.endTime);
        this.dataTable.url = '/test/api/v1/smsList';
        this.$refs.dataTable.$emit('load-data');
      },
      warnTime(startTime, endTime){
        let timeStart = new Date(startTime).getTime();
        let timeEnd = new Date(endTime).getTime();
        if (timeStart > timeEnd) {
          this.$notify({
            title: '警告',
            message: '查询开始时间不能大于结束时间',
            type: 'warning'
          });
          return true;
        }
        return false;
      },
      dealTime(time){
        return formatDate(new Date(time).getTime(), 'yyyy-MM-dd hh:mm:ss');
      },
      formatterDate(row, column){
        return formatDate(row.date, 'yyyy-MM-dd');
      },

    },
  }
</script>
<style>
  .span-style {
    display: inline-block;
    margin-top: 8px;
    font-size: 14px;
  }
</style>
